import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { UseFormSetValue } from 'react-hook-form/dist/types/form';
import { useDispatch, useSelector } from 'react-redux';
import { createCar, fetchCar } from '../../../../store/Cars/thunks';
import { Dispatcher } from '../../../../types/store';
import { CarsState } from '../../../../types/Cars';
import { CarForm } from '../../../../types/Edit';
import fetchLocalImage from '../../../../utils/helpers/fetchLocalImage';
import getBase64 from '../../../../utils/helpers/getBase64';

const getForm = async (form: CarForm, setBody: React.Dispatch<React.SetStateAction<object>>): Promise<void> => {
  const formData = new FormData();
  formData.append('name', form.name);
  formData.append('description', form.description);
  formData.append('priceMin', form.priceMin);
  formData.append('priceMax', form.priceMax);
  formData.append('number', form.number);
  form.colors.forEach((color) => {
    formData.append('colors', color);
  });
  formData.append('categoryId', form.categoryId);
  const base64Image = await getBase64(form.thumbnail[0], (result) => {
    formData.append('thumbnail', result);
  });
  await setBody(formData);
};

export const useSaveCar = (form: CarForm | null, carId: string): [boolean] => {
  const navigate = useNavigate();
  const dispatch = useDispatch<Dispatcher>();
  const { postCar, postLoading, postError } = useSelector<{
    cars: CarsState;
  }, CarsState>((state) => state.cars);
  const [body, setBody] = useState<object | null>(null);

  useEffect(() => {
    if (form) {
      getForm(form, setBody);
    }
  }, [form]);

  useEffect(() => {
    if (body !== null) {
      if (carId) {
        console.log('hm');
      } else {
        dispatch(createCar(body));
      }
    }
  }, [body]);

  useEffect(() => {
    if (postCar) {
      navigate('/cars');
    }
  }, [postCar]);

  return [postLoading];
};

export const useCarById = (carId: string, setValue: UseFormSetValue<CarForm>): [boolean, number] => {
  const dispatch = useDispatch<Dispatcher>();
  const { carById, carByIdLoading, carByIdError } = useSelector<{
    cars: CarsState;
  }, CarsState>((state) => state.cars);

  useEffect(() => {
    if (carId) {
      dispatch(fetchCar(carId));
    }
  }, [carId]);

  const setValuesToForm = async (): Promise<void> => {
    const car = carById;
    const carImage = await fetchLocalImage(car.thumbnail.path, car.thumbnail.originalname);
    await setValue('thumbnail', carImage, { shouldDirty: true });
    setValue('name', car?.name, { shouldDirty: true });
    setValue('categoryId', car?.categoryId?.id, { shouldDirty: true });
    setValue('description', car?.description, { shouldDirty: true });
    setValue('colors', car?.colors, { shouldDirty: true });
    setValue('name', car?.name, { shouldDirty: true });
    setValue('number', car?.number?.toUpperCase(), { shouldDirty: true });
    setValue('priceMin', car.priceMin.toString(), { shouldDirty: true });
    setValue('priceMax', car.priceMax.toString(), { shouldDirty: true });
  };

  useEffect(() => {
    if (carById && carId) {
      setValuesToForm();
    }
  }, [carById]);

  return [carByIdLoading, carByIdError];
};
